<template>
    <el-container class="hub-wrapper">
        <el-header>Header</el-header>
        <el-container>
            <el-aside width="350px">
                <el-menu @select="onSelectChange">
                    <el-submenu v-for="doc of docs" :index="doc.title">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>{{doc.title}}</span>
                        </template>
                        <template v-for="sub of doc.children">
                            <el-submenu v-if="sub.children" :index="sub.title">
                                <template slot="title">
                                    <span>{{sub.title}}</span>
                                </template>
                                <el-menu-item :index="item.sha" v-for="item of sub.children">
                                    <span slot="title">{{item.title}}</span>
                                </el-menu-item>
                            </el-submenu>
                            <el-menu-item v-else :index="sub.sha">
                                <span slot="title">{{sub.title}}</span>
                            </el-menu-item>
                        </template>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main>
                <div class="markdown-body" v-html="html"></div>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import {mapState} from 'vuex';
    import marked from 'marked';
    import highlight from 'highlight.js';

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function (code) {
            return highlight.highlightAuto(code).value;
        },
        pedantic: false,
        gfm: true,
        tables: true,
        breaks: false,
        sanitize: false,
        smartLists: true,
        smartypants: false,
        xhtml: false
    });

    export default {
        name: "Hub",
        created: function () {
            this.repos.forEach(repo => {
                this.$store.dispatch("getTree", repo).then();
            })
        },
        computed: {
            ...mapState(["repos", 'docs', 'markdownMap'])
        },
        methods: {
            onSelectChange: function (sha) {
                const markdown = this.markdownMap.get(sha);
                const repo = markdown.repo;
                this.$store.dispatch('getBlobs', {repo, sha})
                    .then(res => this.html = marked(res));
            }
        },
        data: function () {
            return {
                html: null
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../assets/github-markdown.css";
    @import "../../node_modules/highlight.js/styles/atelier-estuary-light.css";

    .hub-wrapper {
        height: 100%;

        .el-header {
            background-color: #409EFF;
        }


        .el-aside {
            .el-menu {
                height: 100%;
            }
        }

        .el-main {
            height: 100%;

            .markdown-body {
                max-width: 852px;
                margin: 0 auto;
            }
        }
    }

</style>
